<template>
    <Wtext
    v-if="type === 'Wtext'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @input="change"></Wtext>
    <WtextHeader
    v-else-if="type === 'WtextHeader'"
    :value="value"
    :read-only="readOnly"
    @input="change"></WtextHeader>
    <Wphoto
    v-else-if="type === 'Wphoto'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    :init="init"
    @input="change"></Wphoto>
    <WphotoHeader
    v-else-if="type === 'WphotoHeader'"
    :value="value"
    :read-only="readOnly"
    @input="change"></WphotoHeader>
    <Wfile
    v-else-if="type === 'Wfile'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @input="change"></Wfile>
    <Warticle
    v-else-if="type === 'Warticle'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @permit="(e)=>{$emit('permit', e)}"
    @input="change"></Warticle>
    <Wvideo
    v-else-if="type === 'Wvideo'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    :init="init"
    @input="change"></Wvideo>
    <WvideoHeader
    v-else-if="type === 'WvideoHeader'"
    :value="value"
    :read-only="readOnly"
    @input="change"></WvideoHeader>
    <Wmap
    v-else-if="type === 'Wmap'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @permit="(e)=>{$emit('permit', e)}"
    @input="change"></Wmap>
    <Wcopy
    v-else-if="type === 'Wcopy'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @permit="(e)=>{$emit('permit', e)}"
    @input="change"></Wcopy>
    <Wapplet
    v-else-if="type === 'Wapplet'"
    :value="value"
    :read-only="readOnly"
    :option="option"
    @permit="(e)=>{$emit('permit', e)}"
    @input="change"></Wapplet>
</template>

<script>
    import Wtext from "./items/text/index.vue"
    import WtextHeader from "./items/text/header.vue"
    import Wphoto from "./items/photo/index.vue"
    import WphotoHeader from "./items/photo/header.vue"
    import Wfile from "./items/file/index.vue"
    import Warticle from "./items/article/index.vue"
    import Wvideo from "./items/video/index.vue"
    import WvideoHeader from "./items/video/header.vue"
    import Wmap from "./items/map/index.vue"
    import Wcopy from "./items/copy/index.vue"
    import Wapplet from "./items/applet/index.vue"
    export default {
        components: {
            Wtext,
            WtextHeader,
            Wphoto,
            WphotoHeader,
            Wfile,
            Warticle,
            Wvideo,
            WvideoHeader,
            Wmap,
            Wcopy,
            Wapplet
        },
        props: {
            type: {
                type: String,
                default: undefined,
            },
            value: {
                type: [String, Number, Object, Array, Boolean],
                default: undefined,
            },
            option: {
                type: Object,
                default: undefined,
            },
            readOnly: {
                type: Boolean,
                default: false
            },
            init: {
                type: Boolean,
                default: false
            },
        },
        data() {
            return {
                hasPermitType: ['Wcopy','Warticle','Wmap','Wapplet']
            }
        },
        methods: {
            change(e) {
                this.$emit('input', e)
            }
        },
        watch: {
            type: {
                deep: true,
                immediate: true,
                handler(e) {
                    if(this.hasPermitType.indexOf(e) == -1 && !this.readOnly){
                        this.$emit('permit', true)
                    }
                },
            }
        },
    }
</script>

